<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级简介设置</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="save">保存</span>
        </div>
        <div class="c333 f28" style="padding:0.84rem 0 1rem;">
            <div class="nameBox">
                <van-uploader :after-read="onRead">
                    <img class="bs" :src="$api.fileUrl + data.classIamge" v-if="data.classIamge" style="width:100%;height:100%;">
                    <div class="pt-90 pb-90 bs" v-else>
                        <img src="../../../assets/class/upLoadImg.png" alt="">
                        <p class="pt-60 cccc f32">上传封面</p>
                    </div>
                </van-uploader>
            </div>
            <div class="content b_line">
                <p class="subtitle">班级简介</p>
                <textarea class="c666" v-model="data.classDesc" placeholder="请输入简介内容..."></textarea>
            </div>
            <div class="content">
                <p class="subtitle">图片</p>
                <div class="picture">
                    <img :src="$api.fileUrl + item" alt="" v-for="(item,index) in data.imageUrl" :key="index" @click="show(item)">
                    <van-uploader :after-read="onReadImg"  v-if="data.imageUrl.length < 10" accept="image/png, image/jpeg">
                        <img src="../../../assets/my/upload.png" alt="">
                    </van-uploader>
                </div>
                <p class="subtitle">视频</p>
                <div class="picture">
                    <video :src="$api.fileUrl + item" alt="" v-for="(item,index) in data.videoUrls" :key="index"></video>
                    <van-uploader class="ml-10" :after-read="onReadVideo" style="" v-if="data.videoUrls.length < 1" accept="video/*">
                        <img src="../../../assets/my/upload.png" alt="">
                    </van-uploader>
                </div>
            </div>  
        </div>
    </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            data:{
                classIamge:'',
                classDesc:'',
                imageUrl:[],
                videoUrls:[]
            }
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        show(url){
            ImagePreview([this.$api.fileUrl + url]);
        },
        save(){
            if(this.data.classIamge == ''){
                this.$toast('请上传封面');
                return
            }
            if(this.data.classDesc == ''){
                this.$toast('请输入班级简介');
                return
            }
            var that = this;
            var param = {
                classIamge : that.data.classIamge ,
                id:JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                classDesc: that.data.classDesc,
                imageUrls:that.data.imageUrl.toString(),
                videoUrls:that.data.videoUrls.toString(),
            }
            this.$http_json(this.$api.class.classIntroEdit,param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$router.push({path:"/classIntro"});
                }else{
                    this.$toast('编辑失败了！');
                }
             })
            // 
        },
        onRead(file) {//上传图片
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data.classIamge = data.data[0];
                }
            })
        },
        onReadImg(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data.imageUrl.push(data.data[0]);
                }
            })
        },
        onReadVideo(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data.videoUrls.push(data.data[0]);
                }
            })
        },
        getClassInfo(){//获取班级信息
            this.$http({url:this.$api.class.classIntro},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data = data.data;
                }else{
                    this.$toast(data.msg);
                }
            })
        },
    },
    
    created() {
        this.getClassInfo();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
        z-index: 999;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:.3rem;
    }
    .nameBox div{
        width:100%;
        height:3.88rem;
        box-shadow: 0 0 0.3rem #ccc;
        border-radius: 0.1rem;
        text-align: center;
    }
    .nameBox img{
        width:1rem;
    }
    .name{
        border:0;
        text-align: right;
        width:50%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:2.67rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-left: 0.1rem;
    }
    .picture img:first-child{
        margin-left: 0;
    }
    .picture video{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-left: 0.1rem;
        float: left;
    }
    .picture video:first-child{
        margin-left: 0;
    }
</style>




